<template>
    <div class="machines">
        <h1>Machines</h1>

        <table class="table">
            <thead>
                <tr>
                    <th scope="col">Id</th>
                    <th scope="col">TierType</th>
                    <th scope="col">SubTierType</th>
                    <th scope="col">StateType</th>
                    <th scope="col">CpuCount</th>
                    <th scope="col">LastRan</th>
                    <th scope="col">Terminated</th>
                    <th scope="col">GPUS</th>
                    <th scope="col"></th>
                </tr>
            </thead>

            <tbody>
                <SingleMachine v-for="mac in machines" :key="mac.id" v-bind:machine="mac"/>
            </tbody>
        </table>

        <button id="load-machines" class="btn btn-primary" @click="loadMachines">Load machines</button>
        <button
            id="add-new-machine"
            class="btn-margin btn btn-primary"
            @click="addNewMachine"
        >Add new machine</button>
    </div>
</template>

<script>
// import json from "../data.json";
import SingleMachine from "./SingleMachine.vue";
import axios from "axios";

export default {
    components: { SingleMachine },
    data: function() {
        return {
            machines: []
        };
    },

    methods: {
        loadMachines: function() {
            axios
                .get("http://localhost:8888/api/machines/get")
                .then(res => (this.machines = res.data));
            // this.machines = JSON.parse(JSON.stringify(json["machines"]));
        },

        addNewMachine: function() {
            let tierTypes = ["S", "T1", "T2", "G1", "G2"];

            let subTierTypes = {
                S: ["MICRO"],
                T1: ["SMALL", "MEDIUM"],
                T2: ["MEDIUM", "LARGE"],
                G1: ["LARGE"],
                G2: ["LARGE"]
            };

            // let cpus = {
            //     "S-MICRO": 1,
            //     "T1-SMALL": 1,
            //     "T1-MEDIUM": 2,
            //     "T2-MEDIUM": 3,
            //     "T2-LARGE": 4,
            //     "G1-LARGE": 4,
            //     "G2-LARGE": 8
            // };

            let gpus = {
                "G1-LARGE": ["SINGLE",1],
                "G2-LARGE": ["CLUSTER",4]
            };

            let machine = {
                tierType: "",
                subTierType: "",
                stateType: "",
                cpuCount: 0,
                lastRan: "",
                terminated: "",
                gpuCount: 0,
                gpuTier:""
            };

            machine.tierType =
                tierTypes[Math.floor(Math.random() * tierTypes.length)];

            let allowedTypes = subTierTypes[machine.tierType];
            machine.subTierType =
                allowedTypes[Math.floor(Math.random() * allowedTypes.length)];

            let allowedGpuTypes = gpus[machine.tierType+"-"+machine.subTierType];
            if(allowedGpuTypes){
                machine.gpuTier = allowedGpuTypes[0];
                machine.gpuCount = Math.floor(Math.random() * (allowedGpuTypes[1] + 1));
            }

            var params = new URLSearchParams();
            params.append("tier", machine.tierType);
            params.append("subTier", machine.subTierType);
            params.append("gpuTier", machine.gpuTier);
            params.append("gpuCount", machine.gpuCount);

            axios
                .post("http://localhost:8888/api/machines/create", params)
                .then(res => {
                    this.loadMachines();
                    // console.log(res);
                });

            this.loadMachines();
        }
    }
};
</script>

<style scoped>
.btn-margin {
    margin-left: 20px;
}
</style>
