<template>
    <tr class="machine-row">
        <th class="mac-id" scope="row">{{ machine['id'] }}</th>
        <td class="mac-type">{{ machine['tierType'] }}</td>
        <td class="mac-subtype">{{ machine['subTierType'] }}</td>
        <td class="mac-state">{{ machine['stateType'] }}</td>
        <td class="mac-cpus">{{ machine['cpuCount'] }}</td>
        <td class="small-text mac-last-ran">{{ machine['lastRan'] }}</td>
        <td class="mac-terminated">{{ machine['terminated'] }}</td>
        <td class="mac-gpus">{{ machine['gpuCount'] }}</td>
        <td class="mac-actions">
            <button @click="startMachine" v-show="isStopped" class="btn btn-sm state-btn btn-success mac-start">Start</button>
            <button @click="stopMachine" v-show="isStarted" class="btn btn-sm state-btn btn-danger mac-stop">Stop</button>
            <button @click="restartMachine" v-show="isStarted" class="state-btn btn btn-sm btn-info mac-restart">Retart</button>
            <button
                @click="attachGpu"
                class="state-btn btn btn-sm btn-info mac-attach"
                :disabled="!supportGpu || !isStopped"
            >AttachGPU</button>
            <button
                @click="deattachGpu"
                class="state-btn btn btn-sm btn-info mac-deattach"
                :disabled="!supportGpu || !isStopped"
            >DettachGPU</button>
        </td>
    </tr>
</template>

<script>
import axios from "axios";

export default {
    props: ["machine"],
    data: function() {
        return {
            maxGpus: {
                "G1-LARGE": 1,
                "G2-LARGE": 4
            }
        };
    },

    computed: {
        isStarted: function() {
            return this.machine["stateType"] == "RUNNING";
        },

        isStopped: function() {
            return this.machine["stateType"] == "STOPPED";
        },

        isTerminated: function(){
            return this.machine["stateType"] == "TERMINATED";
        },

        supportGpu: function() {
            return ["G1", "G2", "G3"].indexOf(this.machine["tierType"]) != -1;
        }
    },

    methods: {
        startMachine: function() {
            if (this.machine["stateType"] != "STOPPED") {
                return;
            }

            // this.machine["stateType"] = "STARTING";

            // setTimeout(() => {
            //     this.machine["stateType"] = "RUNNING";
            //     this.machine["lastRan"] = moment().format("YY-MM-DD hh:mm:ss");
            // }, 1000);

            axios
                .get("http://localhost:8888/api/machines/start/"+this.machine["id"])
                .then(res => {
                    this.machine = res.data;
                });
        },

        stopMachine: function() {
            if (this.machine["stateType"] != "RUNNING") {
                return;
            }

            axios
                .get("http://localhost:8888/api/machines/stop/"+this.machine["id"])
                .then(res => {
                    this.machine = res.data;
                });
        },

        attachGpu: function() {
            if (this.machine["stateType"] != "STOPPED") {
                return;
            }

            let gpus = parseInt(prompt("Number of gpus to be attached"));

            if (isNaN(gpus) || gpus < 0) gpus = 0;

            let gpuType =
                this.machine["tierType"] + "-" + this.machine["subTierType"];

            if (this.machine["gpuCount"] + gpus > this.maxGpus[gpuType])
                gpus = 0;

            this.machine["gpuCount"] += gpus;
        },

        deattachGpu: function() {
            if (this.machine["stateType"] != "STOPPED") {
                return;
            }

            let gpus = parseInt(prompt("Number of gpus to be attached"));

            if (isNaN(gpus) || gpus < 0 || gpus > this.machine["gpuCount"])
                gpus = 0;

            this.machine["gpuCount"] -= gpus;
        },

        restartMachine: function() {
            if (this.machine["stateType"] != "RUNNING") {
                return;
            }

            axios
                .get("http://localhost:8888/api/machines/restart/"+this.machine["id"])
                .then(res => {
                    this.machine = res.data;
                });
        }
    }
};
</script>

<style scoped>
.state-btn {
    margin-left: 10px;
    margin-top: 5px;
}
</style>
